.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .card {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    padding: 16px;
  }
  
  .close-button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 8px;
    padding: 4px 8px;
    font-size: 16px;
    font-weight: bold;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    color: #000;
  }
.card.custom-border {
  border: 1px solid #e0e0e0;
  border-radius: 24px !important;
  /* el borde sale cortado */
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 2px 2px -1px rgba(0, 0, 0, 0.2);
}
